<template lang="pug">
  .content(v-if="track && track.album")
    p.is-128x128
      img(:src="track.album.images[0].url")
    p
      strong {{ track.name }}
      small  [ {{ track.duration_ms | ms-to-mn }} ]
      p Reproduciendo
    p
      audio(controls, :src="track.preview_url")
</template>

<script>
import { mapState } from 'vuex'
export default {
  // name: 'Player',
  /* data () {
    return {
      track: {}
    }
  },
  created () {
    this.$bus.$on('set-track', (track) => {
      this.track = track
    })
  }, */
  computed: {
    ...mapState(['track'])
  }
}
</script>

<style lang="scss" scoped>
img {
  width: 124px;
  border-radius: 50%;
  padding: 2px;
}
</style>



